.layout {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.header-zone {
  flex: 1;
}

.body-zone {
  flex: 1;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.body-zone div {
  border: 1px solid gray;
}

.menu {
  flex: 2;
}

.content-view {
  flex: 7;
}

.summary {
  flex: 1;
}
